<template>
  <transition name="fade">
    <div class="blackout" v-show="active" @click="$emit('click', false)"></div>
  </transition>
</template>

<script>
export default {
  name: 'c-blackout',

  model: {
    prop: 'active',
    event: 'click'
  },

  props: {
    active: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style scoped>
  .blackout {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.3);
    cursor: pointer;
  }
</style>